import { Button, Drawer, Form, Input, Modal, Radio, Space, Upload, UploadFile, UploadProps, message } from "antd";
import { CloseOutlined, PlusOutlined } from "@ant-design/icons";
import { useState } from "react";
import { RcFile } from "antd/es/upload";
import { addUser } from "@/api/modules/system/user";

interface Props {
	open: boolean;
	onSuccess: () => void;
	onClose: () => void
}

const getBase64 = (file: RcFile): Promise<string> =>
	new Promise((resolve, reject) => {
		const reader = new FileReader();
		reader.readAsDataURL(file);
		reader.onload = () => resolve(reader.result as string);
		reader.onerror = (error) => reject(error);
	});

const AddUserPage = (props: Props) => {
	const [form] = Form.useForm();
	const [avatar, setAvatar] = useState('');
	const [previewOpen, setPreviewOpen] = useState(false);
	const [previewImage, setPreviewImage] = useState('');
	const [previewTitle, setPreviewTitle] = useState('');

	const onFinish = async (values: any) => {

		try {
			await addUser({
				loginName: values.loginName,
				userName: values.userName,
				password: values.password,
				phonenumber: values.phonenumber,
				email: values.email,
				sex: values.sex,
				avatar: avatar,
				status: values.status,
				remark: values.remark
			});
			//清空form
			form.resetFields();
			message.success("创建成功");
			props.onSuccess();
		} catch (error) {
			console.log(error);
		}
	};

	const normFile = (e: any) => {
		if (Array.isArray(e)) {
			return e;
		}
		return e?.fileList;
	};

	const onUploadChange: UploadProps['onChange'] = ({ file, fileList, event }: any) => {
		if (file.status === 'done') {
			if (file.response && file.response.code === 200) {
				setAvatar(file.response.data.fileId);
			}
		}
	}

	const onRemove = () => {
		setAvatar('');
	}

	const handlePreview = async (file: UploadFile) => {
		if (!file.url && !file.preview) {
			file.preview = await getBase64(file.originFileObj as RcFile);
		}

		setPreviewImage(file.url || (file.preview as string));
		setPreviewOpen(true);
		setPreviewTitle(file.name || file.url!.substring(file.url!.lastIndexOf('/') + 1));
	};

	const handleCancel = () => setPreviewOpen(false);

	return (
		<Drawer
			title="添加用户"
			placement='right'
			width={500}
			closable={false}
			open={props.open}
			extra={
				<Space>
					<CloseOutlined onClick={props.onClose} />
				</Space>
			}
		>
			<Form
				labelCol={{ span: 6 }}
				wrapperCol={{ span: 18 }}
				layout="horizontal"
				form={form}
				onFinish={onFinish}
				initialValues={{ sex: '0', status: '0' }}
			>
				<Form.Item label="登录名" name="loginName" rules={[{ required: true }]}>
					<Input placeholder="请输入登录名" />
				</Form.Item>
				<Form.Item label="用户名" name="userName" rules={[{ required: true }]}>
					<Input placeholder="请输入用户名" />
				</Form.Item>
				<Form.Item label="密码" name="password" rules={[{ required: true }]}>
					<Input.Password placeholder="请输入密码" />
				</Form.Item>
				<Form.Item label="确认密码" name="repassword" rules={[{ required: true }]}>
					<Input.Password placeholder="请再次输入密码" />
				</Form.Item>
				<Form.Item label="性别" name="sex" rules={[{ required: true }]}>
					<Radio.Group>
						<Radio value='0'>男</Radio>
						<Radio value='1'>女</Radio>
					</Radio.Group>
				</Form.Item>
				<Form.Item label="邮箱" name="email">
					<Input placeholder="请输入邮箱" />
				</Form.Item>
				<Form.Item label="手机号" name="phonenumber">
					<Input placeholder="请输入手机号" />
				</Form.Item>
				<Form.Item label="状态" name="status" rules={[{ required: true }]}>
					<Radio.Group>
						<Radio value='0'>正常</Radio>
						<Radio value='1'>停用</Radio>
					</Radio.Group>
				</Form.Item>
				<Form.Item label="头像" valuePropName="fileList" getValueFromEvent={normFile}>
					<Upload onPreview={handlePreview} action="http://localhost:3000/file/upload" listType="picture-card" onChange={onUploadChange} maxCount={1} onRemove={onRemove}>
						<PlusOutlined />
					</Upload>
				</Form.Item>
				<Form.Item
					name="remark"
					label="备注"
				>
					<Input.TextArea rows={4} showCount maxLength={100} />
				</Form.Item>
				<Form.Item wrapperCol={{ offset: 6, span: 18 }}>
					<Button type="primary" htmlType="submit">
						确定
					</Button>
				</Form.Item>
			</Form>
			<Modal open={previewOpen} title={previewTitle} footer={null} onCancel={handleCancel}>
				<img alt="example" style={{ width: '100%' }} src={previewImage} />
			</Modal>
		</Drawer>
	);
};
export default AddUserPage;