import { deleteUser, getUserList } from "@/api/modules/system/user";
import { PlusOutlined } from "@ant-design/icons";
import { Avatar, Button, Card, Modal, Space, Table, message } from "antd";
import { ColumnsType } from "antd/es/table";
import { useEffect, useState } from "react";
import CreateUserPage from "./addUser";

interface DataType {
    key: React.Key;
    userId: string;
    loginName: string;
    userName: string;
    sex: string;
    email: string;
    phonenumber: string;
    status: string;
    avatar: any;
    createdAt: string;
}

const UserList: React.FC = () => {

    const [pageNum, setPageNum] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [userTotal, setUserTotal] = useState(0);
    const [userList, setUserList] = useState<DataType[]>([]);
    const [showCreatePage, setShowCreatePage] = useState(false);
    const [showConfirm, setShowConfirm] = useState(false);
    const [actionId, setActionId] = useState('');

    const initUserList = async () => {
        const result = await getUserList({ pageNum, pageSize });
        if (result.data) {
            setUserTotal(result.data.total);
            setUserList(result.data.data as []);
        }
    };

    useEffect(() => {
        initUserList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        initUserList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pageNum, pageSize])

    const columns: ColumnsType<DataType> = [
        {
            title: '登录名',
            dataIndex: 'loginName',
            width: 100,
        },
        {
            title: '用户名',
            dataIndex: 'userName',
            width: 100,
        },
        {
            title: '性别',
            dataIndex: 'sex',
            width: 50,
            render: (_, record) => (
                <Space size="middle">
                    {record.sex === '0' ? '男' : '女'}
                </Space>
            ),
        },
        {
            title: '邮箱',
            dataIndex: 'email',
            width: 100,
        },
        {
            title: '手机号',
            dataIndex: 'phonenumber',
            width: 100,
        },
        {
            title: '状态',
            dataIndex: 'status',
            width: 50,
            render: (_, record) => (
                <Space size="middle">
                    {record.status === '0' ? '正常' : '停用'}
                </Space>
            ),
        },
        {
            title: '头像',
            dataIndex: 'avatar',
            width: 100,
            render: (_, record) => (
                <Space size="middle">
                    {record.avatar ? <Avatar src={record.avatar.url} /> : ''}
                </Space>
            ),
        },
        {
            title: '创建时间',
            dataIndex: 'createdAt',
            width: 100,
        },
        {
            title: '操作',
            width: 100,
            render: (_, record) => (
                <Space size="middle">
                    <Button type="link">修改</Button><Button type="link" danger onClick={e => { setActionId(record.userId); setShowConfirm(true) }}>删除</Button>
                </Space>
            ),
        },
    ];

    const confirmDelete = async () => {
        setShowConfirm(false);
        const result = await deleteUser(actionId);
        if(result.code === 200){
            message.success('删除成功');
            initUserList();
        }
    }

    const cancelDelete = () => {
        setShowConfirm(false);
    }

    const pagination = {
        showSizeChanger: true,
        onChange: (page: number, pageSize: number) => {
            setPageNum(page);
            setPageSize(pageSize);
        },
        defaultCurrent: pageNum,
        total: userTotal
    }
    return (<div>
        <Card>
            <Button type="primary" icon={<PlusOutlined />} onClick={e => setShowCreatePage(true)}>新增用户</Button>
        </Card>
        <Card title="用户列表" style={{ marginTop: '20px' }}>
            <Table rowKey={(record) => record.userId} pagination={pagination} columns={columns} dataSource={userList} />
        </Card>
        <Modal title="提示!" open={showConfirm} onOk={confirmDelete} onCancel={cancelDelete}>
            确认删除？
        </Modal>
        <CreateUserPage onSuccess={() => { setShowCreatePage(false); initUserList(); }} open={showCreatePage} onClose={() => setShowCreatePage(false)}></CreateUserPage>
    </div>);
}

export default UserList;