import http from "../../index"
import { ReqPage, ResPage } from "../../interface";
import { User } from "../../interface/system/user";

// 新增用户
export const addUser = (params: User.CreateUserForm) => {
    return http.post<User.ResUser>('users', params);
};

// 获取用户列表
export const getUserList = (params: ReqPage) => {
    return http.get<ResPage<User.ResUser>>('users', params);
};

// 删除用户
export const deleteUser = (id: string) => {
    return http.delete<ResPage<User.ResUser>>(`users/${id}`);
};